                </div>
              </div>
              <div class="page scolls" :class="getCl(4)" :style="getZindex(4)" @click="clicks(4)">
                <div class="box-content"> Ông bà Lãnh trồng một vườn quít. Hàng xóm thường tụ tập đến đây để đổi chác, mua
