              </div>
              <div class="page scolls" :class="getCl(5)" :style="getZindex(5)" @click="clicks(5)">
                <div class="box-content"> Thuở ấy, chưa có thuốc men như bây giờ nên người ta cho rằng bệnh dịch tá là do
